* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body,
html {
  min-height: 100vh;
  min-width: 100%;
  background: linear-gradient(#237317, #195115);
  background-repeat: no-repeat;
}

.main-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  margin: 0 auto;
  padding: 2rem;
}

.form-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.select {
  /* Helvetica font, */
  padding: 20px;
  text-align: center;
  border-radius: 10px;
  font-size: 44px;
  background-color: inherit;
  color: #ffffff;
  border: 5px solid #ffffff;
}

.when-born {
  /* Heineken Serif 18 : */
  font-weight: bold;
  font-size: 44.7px;
  color: #ffffff;
  text-align: center;
}

.tc-para {
  /* Helvetica Font, */
  font-size: 31.81;
  color: #ffffff;
}

.dob {
  /* Helvetica Font */
  width: auto;
  font-size: 46.3px;
  color: #ffffff;
  border: 5px solid #ffffff;
  text-align: center;
  border-radius: 10px;
  font-size: 44px;
  background-color: inherit;
}

.dob::placeholder {
  color: white;
  opacity: 1;
}

.enter-btn {
  font-size: 26.3px;
  color: #ffffff;
  border: 5px solid #ffffff;
  text-align: center;
  border-radius: 10px;
  font-size: 44px;
  background-color: inherit;
  width: 80%;
}

.date-div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer-div {
  bottom: 0;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;

}